import {createEnhancedElement} from '@servicenow/library-enhanced-element';
import {dangerouslyCreateElementFromString} from '@servicenow/ui-renderer-snabbdom';
import toString from 'lodash/toString';

/*
 * TODO: This is a temporary way of debugging customer problems during the
 * Seismic 18 upgrade process, because tectonic resolution makes it hard to
 * know what version of a component is running. Remove when not needed.
 * Intentionally runs in dev + production environments so we can debug all builds.
 */
const {name, version} = require('../package.json');
const registry = (window.___NOW_DESIGN_SYSTEM_PACKAGES___ =
	window.___NOW_DESIGN_SYSTEM_PACKAGES___ || {});
if (!registry[name]) {
	registry[name] = version;
}

const view = ({properties: {html}}) => {
	const htmlStr = toString(html);
	if (!htmlStr) {
		return null;
	}

	// `dangerouslyCreateElementFromString` will return an object for only the first element
	// in the string, which means if `htmlStr` consists of multiple siblings,
	// only the first is returned. Wrapping in a root <div> to ensure all
	// siblings are returned (as that <div>'s children array).
	return dangerouslyCreateElementFromString(`<div>${htmlStr}</div>`).children;
};

/**
 * Renders HTML content as-is, without sanitizing it or ensuring its safety.
 * Recommended for use in UI Builder. If using a different interface,
 * use JSX or content slots instead.
 *
 * ```jsx
 * <now-rich-text html="<strong>Hello!</strong> <a href='url'>Click me!</a>">
 * </now-rich-text>
 * ```
 *
 * @seismicElement now-rich-text
 * @summary Renders HTML content.
 * @uib.label Rich text
 * @uib.icon rich-text
 * @uib.description Renders custom HTML
 * @uib.category primitives
 * @uib.associatedTypes global.core, global.dashboard, global.landing-page
 * @uib.properties html
 */
createEnhancedElement('now-rich-text', {
	properties: {
		/**
		 * HTML content to render.
		 * @type {string}
		 * @uib.label HTML
		 * @uib.description HTML content to render in the component
		 * @uib.required
		 * @uib.fieldType html
		 * @uib.defaultValue <p>We make work, work better for people with modern digital workflows</p>
		 */
		html: {required: true, schema: {type: 'string'}}
	},
	view
});
